import React from 'react';
import { HashRouter, Switch, Route, Redirect } from 'react-router-dom';
import './App.css';

import { Container, Row, Col } from 'reactstrap';

import TopRouter from './ResumeWebsite/components/TopRouter';
import HomePage from './ResumeWebsite/HomePage';
import CareerPage from './ResumeWebsite/CareerPage';

function App() {
  return (
    <div>
      <HashRouter>
        <Container fluid>
          <TopRouter/>
          <Row>
            <Col xs="12">
              <Switch>
                <Route
                  path="/Home"
                  render={() => 
                    <HomePage/>
                  }
                />
                <Route
                  path="/Career"
                  render={() => 
                    <CareerPage/>
                  }
                />
                <Redirect from="/" to="/Home" />
              </Switch>
            </Col>
          </Row>
        </Container>
      </HashRouter>
    </div>
  );
}

export default App;
