import React from 'react';
import { Link } from 'react-router-dom';
import { Row, Col } from 'reactstrap';

const TopRouter = () => {
  return (
    <div>
      <Row>
        <Col xs="3">
          <Link to="/Home">
            <h4 style={{fontFamily: 'Roboto'}}>
              Home
            </h4>
          </Link>
        </Col>
        <Col xs="3">
          <Link to="/Career">
            Career
          </Link>
        </Col>
        <Col xs="3">
          <Link to="/Projects">
            Projects
          </Link>
        </Col>
        <Col xs="3">
          <Link to="/Scrapbook">
            Scrapbook
          </Link>
        </Col>
      </Row>
    </div>
  )
}

export default TopRouter;